.type-container {
  text-align: center;
  color: #666;
  height: 100vh;
  text-align: center;
  font-size: 2rem;
}

input {
  width: 40%;
  height: 50px;
  text-align: left;
  border-radius: 5px;
  font-size: 1em;
  font-weight: 600;
  -webkit-transition: all .2s ease-in;
  -o-transition: all .2s ease-in;
  transition: all .2s ease-in;
}


input::-webkit-input-placeholder {
  padding-left: 5px;
}


input:-ms-input-placeholder {
  padding-left: 5px;
}


input::-ms-input-placeholder {
  padding-left: 5px;
}


input::placeholder {
  padding-left: 5px;
}

.instructions {
  background: lightgray;
  width:40%;
  min-height: 100px;
  margin: 0 auto;
  margin-top: 5%;
  font-size: .7em;
}

#word {
  font-size: 5.5rem;
}

.instructions h3 {
  font-weight: 600;
}

.instructions p {
  font-size: 1.3em;
}

#counter {
  color: #72ff4f;
}




